<template>
  <div class="header">
    <div class="inner">
      <div class="logo" @click="$emit('goHome')">
        <h1>vue-js</h1>
      </div>
      <div v-if="!userInfo" class="login">
        <input type="text" v-model="val" />
        <button @click="login"></button>
      </div>
      <div v-else class="logout">
        <img :src="userInfo.avatar_url" alt />
        <button @click="logout"></button>
      </div>
      <div class="addArticle">
        <span v-if="userInfo&&this.$route.fullPath.indexOf('topic/create')===-1">
          <router-link :to="`${$publicUrl}/topic/create`"></router-link>
        </span>
      </div>
      <div class="header-nav">
        <div class="nav-ul">
          <div class="nav-li">
            <router-link :to="`${$publicUrl}`"></router-link>
          </div>
          <div v-if="userInfo" class="nav-li spanInLi">
            <router-link :to="`${$publicUrl}/my/messages`">
              <span v-if="messageNum">{{messageNum}}</span>
            </router-link>
          </div>
          <div class="nav-li"></div>
          <div class="nav-li">vue-2.0</div>
          <div class="nav-li"></div>
          <div class="nav-li">API</div>
          <div class="nav-li"></div>
          <div class="nav-li"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//    1.cookie 2.localStorage 3.sessionStorage
// local  session local   session  
//       
// : localStorage.setItem('','')   sessionStorage.setItem('','')
// : localStorage.remove("")    localStorage.clear() 
// sessionStorage.remove("")   sessionStorage.clear() 

//  localStorage.getItem('')  sessionStorage.getItem('')
//  boolean number string
//  F12    Application 
import axios from "axios";
export default {
  name: "tou",
  data() {
    return {
      val: "969bace2-ba1c-40e5-bfe3-750148f06844",
      userInfo: null,
      messageNum: 0
    };
  },
  created() {
    if (sessionStorage.getItem("token")) {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: this.val
        })
        .then(res => {
          console.log(res.data);
          this.userInfo = res.data;
        });
    }
  },
  watch: {
    "$route.fullPath": {
      immediate: true,
      handler() {
        if (sessionStorage.getItem("token")) {
          axios
            .get(
              `https://www.vue-js.com/api/v1/message/count?accesstoken=${sessionStorage.getItem(
                "token"
              )}`
            )
            .then(res => {
              this.messageNum = res.data.data;
              console.log(res.data.data);
            });
        }
      }
    }
  },
  methods: {
    login() {
      this.$router.push(`${this.$publicUrl}`);
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: this.val
        })
        .then(res => {
          //  
          this.userInfo = res.data;
          sessionStorage.setItem("token", this.val);
          sessionStorage.setItem("user_id", res.data);
        });
    },
    logout() {
      this.$router.push(`${this.$publicUrl}`);
      this.userInfo = null;
      sessionStorage.clear();
    }
  }
};
</script>

<style>
.header {
  background-color: #1c6132;
}
.header .inner {
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
}
.header .inner > div {
  margin-right: 20px;
  display: flex;
  align-items: center;
}
.header .inner .logo {
  background-image: url("./../assets/logo.png");
  background-repeat: no-repeat;
  background-size: contain;
  height: 50px;
}
.header h1 {
  margin-left: 60px;
  margin-top: 0;
  margin-bottom: 0;
  color: #fff;
  font-size: 24px;
}
.header input {
  outline: none;
  border-radius: 5px;
  padding: 0 8px;
}
.logout img {
  width: 45px;
}
.logout button {
  align-self: flex-end;
}
.addArticle span {
  display: block;
  background-color: #fff;
  border-radius: 5px;
  background-color: rgb(64, 127, 163);
}
/* .header-nav {
  flex-grow: 1;
} */
.nav-ul {
  display: flex;
}
.nav-ul .nav-li {
  margin: 0 5px;
  color: #fff;
  font-size: 14px;
}
.nav-ul .nav-li a {
  color: #fff;
}
.nav-ul .spanInLi a {
  display: flex;
  color: #fff;
}
.nav-ul .spanInLi span {
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #369219;
  color: #fff;
  text-align: center;
}
</style>
